.eventsParent{
    width: 100%;
    height: calc(100vh - 50px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    bottom: 0px;
}
.eventCardContainer{
    height:75vh;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content:center;
    align-items: baseline;
}
.eventCardContainerMobile{
    height:auto;
    width: auto;
    display: flex;
    flex-direction: column;
    justify-content:center;
    align-items:center;
}
.eventsHeading{

   
        text-align: center;
        font-family: 'Tangerine', sans-serif;
        font-size: max(1.8vw, 40px);
        margin-top:7px;
      
}
.eventsContainer{
    width: 100%;
    height:100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: max(1vw, 12px);
}
.downloadLink{
    margin:4px;
    color: rgb(32, 0, 148);
    background-color: white;
    border: none;
    text-decoration: none;
    display: flex;
    flex-direction: row;
    justify-content: baseline;
    align-items: baseline;
    padding: 10px;
   width:auto;
    font-size: 16px;
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,.2);
  }
  .downloadLink:hover{
    transform: translateY(1px);
    text-decoration: underline;
  }