.navbar {
background-color: #343a40 ;
height:50px;
width: 100vw;
z-index:1;
display: flex;
flex-direction: row;
justify-content: flex-start;
align-items: center;
position: fixed;


}
.fullNavButton{
    background-color: #4caf4f00;
    width: auto; 
    font-size: 15px;
    height:50px;
    border: none;
    color: rgba(255, 254, 254, 0.856);
    text-align: left;
    font-family:'Times New Roman', Times, serif;
    margin:5px;
    

}
.fullNavButton:focus{
    background-color: #4caf4f00;
    border: none;
    color: white;
    text-align: center;
    border-bottom: #dd4124 solid 2px;

  

}
.fullNavButton:hover{
   transform: translateY(-1px);
   color:white;
   cursor:pointer;
   

}
.toggleBtn{
    height:30px;
    width: 40px;
    padding:10px;
   
}
.fullNavBar{
    display:flex;
    flex-direction: row;
    align-items: center;
    width:100%;
    justify-content: flex-start;

}
.navButtonContainer {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    height:100%;
}
.navButton{
    display: flex;
    justify-content: center;
    width: 100%;
    height: 50px;
    background-color: transparent;
    border: none;
    align-items: center;
    font-family: 'Times New Roman', Times, serif;
    font-size: 16px;

    
}
.navButton:hover{
    transform: translateY(-1px);
    cursor:pointer;
}
.navButton:focus{
   color: white
}
.unionGapLogo{

    margin-left: 0px;
    margin-right: 0px;
    width:150px;
    margin:20px;
    border-radius: 100%;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}
.letterHeadFullscreen{
    height:50px;
    width: auto;
   margin-right:10px;
   animation-name: slideInRight;
animation-duration: 300ms;
animation-iteration-count: 1;

}
.letterHeadMobile{
    height:40px;
  position: fixed;
   right:0px;
 
   margin-right:9px;
}
.fbLink{
    width:auto;
    height:16px;
position: fixed;
right:95px;
animation-name: slideInLeft;
animation-duration: 1000ms;
animation-iteration-count: 1;
}
.fbLink:hover{
    cursor:pointer;
}
.fbLinkMobile{
    width:auto;
    height:16px;
    margin:15px;
}
.fredMeyer{
    width: auto;
    height: 40px;
    position: fixed;
    margin-right: 0px;
    right:5px;
    animation-name: slideInLeft;
    animation-duration: 500ms;
    animation-iteration-count: 1;
}
.fredMeyer:hover{
    cursor: pointer;
}
.fredMeyerMobile{
    width: auto;
    height: 50px;
    margin-right: 0px;
}
@keyframes slideInLeft{
  
    0%{
        transform: translateX(100%);
    }
}
@keyframes slideInRight{
  
    0%{
        transform: translateX(-200%);
    }
   

}
@keyframes slideInTop{
  
    0%{
        transform: translateY(-100%);
    }
}

