.sponsorsParent{
    height: calc(100vh - 50px);
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.sponsorsContainer{
    width:100%;
    height:auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}
.logoContainer{
    width:100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: baseline;
    margin: 10px;
}
.logo{
    width:25%;
    max-width: 300px;
    height:auto;
    min-width: 150px;
    margin: 10px;
}
.sponsorsHeading{
    text-align: center;
    font-family: "Tangerine", sans-serif;
    font-size: min(8vw, 40px);
}
.sponsorP{
    font-family: 'Times New Roman', Times, serif;
    font-size: min(2.3vh, 20px);
    text-align: center;
    margin: 0;
}