  #patron{

  }
 

  #progressBar{
    margin:20px;
    width: 80%;
    max-width: 500px;
  }
 

  * {
    box-sizing: border-box;
  }
  


  input,
  button {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    outline: none;
    border-style: none;
  }
  


  

  

  
  @keyframes fade {
    from {
      opacity: 0;
      transform: scale3D(0.95, 0.95, 0.95);
    }
    to {
      opacity: 1;
      transform: scale3D(1, 1, 1);
    }
  }
  .formHeading{
    float: center;
    text-align: center;
    margin:3px;
  }
  .Form {
    animation: fade 200ms ease-out;
    width:100%;
    max-width: 700px;
    margin-top: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0px;
  }
  .shadowCover{
    width:100%;
    height:45vh;
    background-color: #343a40 ;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    
 

  }
  .donationInstructions{
    color:white;
    text-align: center;
    padding-left:5px;
    padding-right:5px;
    font-size: min(3.9vw, 16px);
    margin-top: 0px;
    line-height: 140%;
  }
  .comingSoon{
    font-size: 40px;
    text-align: center;
    color: white;
    font-family: "Tangerine";
  }
  
  .FormGroup {
    margin: 0 15px 20px;
    padding: 0;
    border-style: none;
    background-color: white;
    will-change: opacity, transform;
    box-shadow: 0 6px 9px rgba(50, 50, 93, 0.06), 0 2px 5px rgba(0, 0, 0, 0.08),
      inset 0 1px 0 white;
    border-radius: 4px;
  }
  
  .FormRow {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    margin-left: 15px;
    border-top: 1px solid #74a59e;
  }
  
  .FormRow:first-child {
    border-top: none;
  }
  
  .FormRowLabel {
    width: 15%;
    min-width: 70px;
    padding: 11px 0;
    color: #988c88;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
 
  }
  
  @keyframes void-animation-out {
    0%,
    to {
      opacity: 1;
    }
  }
  .FormRowInput:-webkit-autofill {
    -webkit-text-fill-color: black;
    /* Hack to hide the default webkit autofill */
    transition: background-color 100000000s;
    animation: 1ms void-animation-out;
  }
  
  .FormRowInput {

    font-size: 16px;
    width: 100%;
    padding: 11px 15px 11px 0;
    color:black;
    background-color: transparent;
    animation: 1ms void-animation-out;
    z-index: 100;
  }
  
  .FormRowInput::placeholder {
    color: #dcd9cb;
  }
  
  .StripeElement--webkit-autofill {
    background: transparent !important;
  }
  
  .StripeElement {
    width: 100%;
    padding: 11px 15px 11px 0;
  }
  .payButtonContainer{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items:center;
    width:100%;
    margin: 15px;
  }
  .amountButton{
    display: block;
    font-size: 16px;
    width: 20%;
    height: 40px;
    margin: 0 5px 0;
    background-color: white;
    box-shadow: 0 6px 9px rgba(0, 0, 0, 0.1), 0 2px 5px rgba(0, 0, 0, 0.08);

    border-radius: 4px;
    color: black;
    font-weight: 600;
    cursor: pointer;
    margin-top: 5px;
    transition: all 100ms ease-in-out;
    will-change: transform, background-color, box-shadow;
  }
  .amountButton:focus{
    display: block;
    font-size: 16px;
    width: 20%;
    height: 40px;
    margin: 0 5px 0;
    background-color: #74a59e;
    box-shadow: 0 6px 9px rgba(0, 0, 0, 0.06), 0 2px 5px rgba(0, 0, 0, 0.08);

    border-radius: 4px;
    color: black;
    font-weight: 600;
    cursor: pointer;
    transition: all 100ms ease-in-out;

  }
  .SubmitButton {
    display: block;
    font-size: 16px;
    width: 100%;
    height: 40px;
    padding: 0px;
    margin: 0 0px 0;
    background-color: #74a59e;
    box-shadow: 0 6px 9px rgba(50, 50, 93, 0.06), 0 2px 5px rgba(0, 0, 0, 0.08),
      inset 0 1px 0 #74a59e;
    border-radius: 4px;
    color: #fff;
    font-weight: 600;
    cursor: pointer;
    transition: all 100ms ease-in-out;
    will-change: transform, background-color, box-shadow;
  }
  
  .SubmitButton:active {
    background-color: #8296d9;
    box-shadow: 0 6px 9px rgba(50, 50, 93, 0.06), 0 2px 5px rgba(0, 0, 0, 0.08),
      inset 0 1px 0 #d63dc2;
    transform: scale(0.99);
  }
  
  .SubmitButton.SubmitButton--error {
    transform: translateY(40px);
  }
  .SubmitButton.SubmitButton--error:active {
    transform: scale(0.99) translateY(40px);
  }
  
  .SubmitButton:disabled {
    opacity: 0.5;
    cursor: default;
    background-color: #7795f8;
    box-shadow: none;
  }
  
  .ErrorMessage {
    color: rgb(3, 3, 3);
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 15px;
    font-size: 13px;
    margin-top: 0px;
    width: 100%;
    max-width: 700px;
    transform: translateY(-15px);
    opacity: 0;
    animation: fade 150ms ease-out;
    animation-delay: 50ms;
    animation-fill-mode: forwards;
    will-change: opacity, transform;
  }
  
  .ErrorMessage svg {
    margin-right: 10px;
  }
  
  .Result {
    margin-top: 50px;
    text-align: center;
    animation: fade 200ms ease-out;
  }
  
  .ResultTitle {
    color: #fff;
    font-weight: 500;
    margin-bottom: 8px;
    font-size: 17px;
    text-align: center;
  }
  
  .ResultMessage {
    color: #9cdbff;
    font-size: 14px;
    font-weight: 400;
    margin-bottom: 25px;
    line-height: 1.6em;
    text-align: center;
  }
  
  .ResetButton {
    border: 0;
    cursor: pointer;
    background: transparent;
  }
 