.parentContainerBuilding{
    height: calc(100vh - 50px);
    background-color: rgb(252, 249, 247);
    z-index: 0;
    display: flex;
    flex-direction: row-reverse;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: flex-start;
    background: url("../images/graaphPaper.jpg");
  }
  .parentContainerBuildingMobile{
    height: calc(100vh - 50px);
    background-color: rgb(252, 249, 247);
    background: url("../images/graaphPaper.jpg");
    z-index: 0;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: flex-start;
    bottom:0px;


  }
  .carousel{
    height: 100%;
    width: 100%;
    max-width: 100vw;
    height: calc(65vh - 50px);
    min-width:360px;
    z-index:0;
  }
  .carouselParent{
 display: fixed;
    height: calc(100vh - 50px);
    min-width:360px;
    width: 50vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

  }

  .slideshowImage{
    height:auto;
    width: 50vw;
    opacity: 1;
    animation: wipeLeft;
    animation-duration: 4000ms;
    animation-delay: -100ms;
    animation-iteration-count: infinite;
    overflow: hidden;
   
  }

  .pageCounter{
    color:black;
  

  }
  .pageCounterMobile{
    color:black;
    position:relative;
    bottom:93%;
    left:43%;
  }
  .slideshowImageMobile{
    height: 50vh;
    width: 100vw;
    max-height: calc(100vw*.75);
    position: relative;
    bottom:-2em;
  }
  .carouselParentMobile{
    height:100vh;
    min-width:260px;
    max-height: calc(100vw*.75);
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

  }
  
  .designInfoParent{
    min-width: 360px;
    height:100%;
    width: 50vw;
   display: flex;
   flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
   
  }
  .buildingP{

    font-size: min(1.2vw, 16px);
    margin:10px;
    text-align: left;
    line-height: 140%;
    font-family: 'Times New Roman', Times, serif;
  }
  .buildingPMobile{

    margin:5px;
    text-align: left;
    line-height: 140%;
    font-family: 'Times New Roman', Times, serif;
  }
  .exteriorParent{
    height: 100%;
    width:100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;

    padding:10px;
  }
  .floorPlanParent{

    height:100%;
    width:100%;
   display: flex;
   flex-direction: column;
   flex-wrap: none;
   align-items: center;
   justify-content: flex-start;
   align-items: flex-start;
    padding:10px;
  }
  .floorPlanParentMobile{
    height:100%;
    width:100%;
   display: flex;
   flex-direction: column;
   flex-wrap: none;
   align-items: center;
   justify-content: flex-start;
   text-align: center;
    padding:10px;
    font-family: Cambria,  Times, 'Times New Roman', serif;
 
  }
  .buildingHeader{
   
    text-align: center;
    font-family: 'Tangerine', sans-serif;
    font-size: 40px;
    width:100%;
    min-width: 220px;
    text-align: center;

  }
  
 
  .downloadLinkContainer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin:0px;
    width:100%;
  }
  .downloadLink{
    margin:4px;
    color: rgb(32, 0, 148);
    background-color: white;
    border: none;
    text-decoration: none;
    display: flex;
    flex-direction: row;
    justify-content: baseline;
    align-items: baseline;
    padding: 10px;
   width:auto;
    font-size: 16px;
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,.2);
  }
  .downloadLink:hover{
    transform: translateY(1px);
    text-decoration: underline;
  }
  .leftButton{
    position: relative;
   left:calc(-50% + 20px);
   bottom: calc(25% + 30px);
   height:30px;
    opacity: .6;
  }
  .leftButton:hover{
    position: relative;
   left:calc(-50% + 20px);
   bottom: calc(25% + 30px);
   height:31px;
    opacity: 1;
  }

  .rightButton{
    position: relative;
   left:calc(50% - 20px);
   bottom: calc(25% + 60px);
   height:30px;
    opacity: .6;
    transform: rotate(180deg);
  }
  .rightButton:hover{
    position: relative;
    left:calc(50% - 20px);
   bottom: calc(25% + 60px);
   height:31px;
    opacity: 1;
  }
  .downloadIcon{
    height:18px;
    position: relative;
    margin: 2px;
  }
  @keyframes wipeLeft{
    0%{
     
      opacity: 0;
    }
    20%{
      opacity: 1;
    }
    80%{
      opacity: 1;
    }
    100%{
     
      opacity: 0;
    }
  
 
  }